import React from 'react';
export default class ModularSecond extends React.Component {
    constructor(props){
        super(props);
        this.state = {};
    }
    render() {
        return (
            <div className="news-item-s2"  >
                <div className='J-news'>
                    <h3>{this.props.item.title}</h3>
                    {
                        this.props.item.images?
                        <div className={["img-wrap2" , this.props.item.id].join(' ')}>
                            {this.props.item.images.slice(0, 3).map((urls,val) =>
                                <div  className="img" key={val}>
                                    <div className="pic" imgurl={urls} style={{backgroundImage: "url(https://static.dzkandian.com/images/loadwap.png)"}}>
                                        {/* <img src={urls} alt=""/> */}
                                    </div>
                                </div>
                            )}
                        </div>
                        :<div className={["img-wrap2" , this.props.item.id].join(' ')}>
                            {[0,1,2].slice(0, 3).map((urls,val) =>
                                <div  className="img" key={val}>
                                    <div className="pic"  style={{backgroundImage: "url(https://static.dzkandian.com/images/loadwap.png)"}}>
                                    </div>
                                </div>
                            )}
                        </div>
                    }
                    
                    <p className="tags clearfix">
                        <em className="tag tag-src">{this.props.item.source}</em>
                        <em className="tag tag-view">
                        {this.props.item.updateTime.substring(0, 10)}
                        </em>
                    </p>
                </div>
            </div>
        )
    }
}