
import React from 'react';
import './header.css';
import Axios from 'axios';
let baseURL = '';
if(process.env.NODE_ENV==='development'){
    baseURL = '';
}else if(process.env.NODE_ENV==='production'){
    baseURL =  'http://wap1.prculture.cn';
}else{
    baseURL =  'http://wap0713.prculture.cn';
}
var axios =  Axios.create({
    baseURL: baseURL
});
export default class HeaderContent extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            headerData:[],
            city:'',
            locate:'',
            type:'',
        };
    }

    handleClick(index,flag,type,first) {
        document.title = type;
        
        //console.log(this.props,index,flag,type)
        sessionStorage.setItem('type',type); 
        let scrollDivWidth = document.querySelector(".top-menu-list li").getBoundingClientRect().width;
        let odivW = scrollDivWidth / 2;
        let oNavDiv = document.querySelectorAll(".top-menu-list li");
        let oClienWidth = document.body.clientWidth / 2 || document.documentElement.clientWidth / 2;
        document.querySelector(".top-menu-list").scrollLeft = oNavDiv[index].offsetLeft - oClienWidth + odivW;
        if(!flag){
            if(first==='first'){
                this.props.routerParams.history.replace('/home/index/'+type); 
            }else{
                this.props.routerParams.history.replace('/home/index/'+type+'?time='+new Date().getTime()); 
            }           
            //console.log('switch',new Date().getTime());
            this.setState({
                type:type
            });
        }
    }

    handNewList(e){
        let type = '';
        let falg = false;
        let routerParams = this.props.routerParams.location;
        if(routerParams.pathname){
            let arr = routerParams.pathname.split('/');
            type = arr[3]||'';
        }
        if(routerParams.pathname && routerParams.pathname.indexOf('/home/artical')>-1){
            falg = true;
        }
        //console.log(this.props.routerParams.location,falg)
        this.servequest = axios.get('/a/news.list').then( (res) => {           
            //console.log(res.data.list);
            var randomNum1 = Math.floor(Math.random() * res.data.list.length);
            let list = [];
            res.data.list.forEach((item,index) => {
                if(item===type){
                    randomNum1 = index;
                }    
                try {
                    this.props.onAddClick(item);
                } catch (error) {
                    
                }         
                item === '本地'?list.push(this.state.city):list.push(item);           
            });
            type = list[randomNum1]; 
            this.setState({
                headerData: list,
                type:type
            });
            
            //console.log(randomNum1,falg,type);
            this.handleClick(randomNum1,falg,type,'first')
        }).catch( (error) => {
            console.log(error);
        });
    };
    
    async handgetIp(){
        let city = '';
        let locate = '';
        await  axios.get('https://recommend.funzixun.com/api/ip').then(res => {
            let tmpArr = res.data.data.split('-');
            city = tmpArr[1] || tmpArr[0];
            locate = res.data.data ;
            this.setState({
                city:city,
                locate:res.data.data,
            });
        }).catch( (error) => {
            console.log(error);
        });
        //console.log(this.state ) 
        //console.time('计时器1');

        await  axios.get('/a/news.html',{ params: { type: city }, headers: { zone: encodeURI(locate) } }).then(res => {
            let typeShow = res.data.data.length > 0 ? true : false
            if (typeShow) {
                city = res.data.data[0]['category'] //同步后台返回city
            }
            this.setState({
                city:city,
            });
            //console.timeEnd('计时器1');
        }).catch( (error) => {
            console.log(error);
        });

    }

    async componentWillMount() {
        //await this.handgetIp();
        await this.handNewList(); 
        //console.log(this.props)
    }
    componentWillUnmount() {
        this.servequest = null;
    };
    render() {
        return (
            <header>
                <div className="top-menu">
                    <ul className="top-menu-list">
                        {this.state.headerData.map((item,index) =>
                            <li className={this.state.type === item?'activeClass':''} key={index} onClick={(e) => this.handleClick(index,false,item,'second')}>
                                {item==='看点'?'推荐':item}
                            </li>
                        )}
                    </ul>
                </div>
            </header>
        )
    }
}
