import React from 'react';
import './../content/detailContent.css';
export default class Artical extends React.Component {
    constructor(props){
        super(props);
        this.state = {
        };
        this.pageRange = this.pageRange.bind(this);//分页器范围
        
    }
    pageRange(){
         if (this.props.page < 3) {
            return [1, 2, 3];
        } else if (this.props.content.artical.length - this.props.page < 4) {
            return [
                this.props.content.artical.length - 4,
                this.props.content.artical.length - 3,
                this.props.content.artical.length - 2,
                this.props.content.artical.length - 1
            ];
        } else {
            return [this.props.page - 1, this.props.page, this.props.page + 1];
        }
    }
    backHome(){
        let oNavDiv = document.querySelectorAll(".top-menu-list li");
        let randomNum1 = Math.floor(Math.random() * oNavDiv.length);
        let type =oNavDiv[randomNum1].innerText;
        
        this.props.routerParams.history.replace('/home/index/'+type+'?time='+new Date().getTime());  
        //console.log(oNavDiv[randomNum1].innerText)
    };
    render() {
        return (
                <div style={{padding: "0 10px"}}>
                    { this.props.details?
                        <div className="artical" >
                            <h3 >{this.props.content.title} </h3>
                            <div className="source">
                                <span>{this.props.content.sourceName}</span>
                                <span>{this.props.content.pubTime}</span>
                            </div>
                            {/* 分页的逻辑 */}
                            <div id='content' className='content'  dangerouslySetInnerHTML={{__html:this.props.content.artical[this.props.page-1]}}></div>
                            {/* 不分页如下 */}
                            {/* <div id='content' className='content' dangerouslySetInnerHTML={{__html:this.props.content.content}}></div> */}
                            { this.props.page === this.props.content.artical.length ? 
                                <div  className="copyright">版权声明：本文内容的组织和采编均来自{ this.props.content.sourceName }。</div>:''
                            }
                            {/* 页码切换 */}
                            { this.props.content.artical.length<6 ?
                                <div className="pagination">
                                    <button   className="pageup" disabled={this.props.page===1} onClick={(e) => this.props.pageChangeEvent('back',this.props.page,e)}>
                                        上一页
                                    </button>
                                    {this.props.content.artical.map((item,index3) =>
                                        <button key={index3} onClick={(e) => this.props.pageChangeEvent('moddle',index3+1,e)} className={['page-btn',this.props.page===index3+1?'page-btn-active':''].join(' ')}>
                                            {index3+1}
                                        </button>
                                    )} 
                                    <button onClick={(e) => this.props.pageChangeEvent('next',this.props.page,e)} className="pagedown">
                                        { this.props.page === this.props.content.artical.length ?"下一篇" : "下一页"}
                                    </button>  
                                </div>
                                :<div className="pagination">
                                    <button   className="pageup" disabled={this.props.page===1} onClick={(e) => this.props.pageChangeEvent('back',this.props.page,e)}>上一页</button>
                                    {this.pageRange().map((item2,index4) =>
                                        <button key={index4} onClick={(e) => this.props.pageChangeEvent('moddle',item2,e)} className={['page-btn',this.props.page===item2?'page-btn-active':''].join(' ')}>
                                            {item2}
                                        </button>
                                    )}
                                    {this.props.page<3 || this.props.content.artical.length - this.props.page >3 ?
                                        <img className="ellipses-icon"  width="18"  height="18"  src={require('./../../assets/img/duo.png')} alt="" />
                                        :''
                                    }
                                    <button onClick={(e) => this.props.pageChangeEvent('moddle',this.props.content.artical.length,e)}  className={['page-btn',this.props.page===this.props.content.artical.length?'page-btn-active':''].join(' ')}>
                                        {this.props.content.artical.length}
                                    </button>
                                    <button onClick={(e) => this.props.pageChangeEvent('next',this.props.page,e)} className="pagedown">
                                        { this.props.page === this.props.content.artical.length ?"下一篇" : "下一页"}
                                    </button>  
                                </div>
                            }
                            <button  className="backhome-btn align-center"  onClick={(e) => this.backHome(e)}>
                                返回首页查看更多
                            </button>
                            <h3 className="more-list">热点推荐</h3>
                        </div>
                        :''}
                </div>
        )
    }
}