import React from 'react';
import Artical from './artical/Artical'
import DetailsContent from './content/detailContent';
import Axios from 'axios';
import loadingDialog from './plugin/loadingDialog';
import information from './plugin/information';

let MeScroll = window.MeScroll;

let baseURL = '';
if(process.env.NODE_ENV==='development'){
    baseURL = '';
}else if(process.env.NODE_ENV==='production'){
    baseURL =  'http://wap1.prculture.cn';
}else{
    baseURL =  'http://wap0713.prculture.cn';
}
var axios =  Axios.create({
    baseURL: baseURL
});
export default class App extends React.Component {
    constructor(props,todos){
        super(props);
        this.state = {
            modularData:[],
            city:'',
            locate:'',
            details:false,
            currentPage:1,
            detailContent:{},
        };
        this.handleClick = this.handleClick.bind(this);//首次获取列表数据
        this.upCallback = this.upCallback.bind(this);//上拉加载
        this.detailsClick = this.detailsClick.bind(this);//查看详情
        this.pageChange = this.pageChange.bind(this);//页码或篇章的切换
    };

    handleClick(locate,city) {
        let beforeId = this.props.match.params.id;
        this.setState({
            city:city,
            locate: locate,
        });
        this.detailsClick(beforeId);
        
    };

    detailsClick(id,e){//详情内容
        //console.log('全局的');
        this.setState({
            modularData:[],
            details:false,
            currentPage:1
        });
        loadingDialog("show"); 
        
        let type = this.props.match.params.type;
        this.props.history.replace('/home/artical/'+type+'/'+id);
        let data = 'type='+type+'&id='+id+'&tpl=json';
        axios.get('/a/news-' + id + ".html?"+data).then( (res) => {  
            //console.log(res.data);
            information(1000,false,id)
            //console.log(res.data.content)
            document.title = res.data.title;
            res.data.content = res.data.content
            .replace(/data-src/g, "imgurl")
            .replace(/<img/g, "<img src\=\'https\:\/\/static\.dzkandian\.com\/images\/loadwap\.png\'")
            .replace(/left/g, "p")
            .replace(/figure/g, "p");
            let tempArr = res.data.content.match(
                /(<p\b[^<>]*?>[\s\S]*?<\/p>[\s\S]*?[^<img[\s\S]*?>][\s\S]*?<p\b[^<>]*?>[\s\S]*?<\/p>)|(<p\b[^<>]*?>[\s\S]*?<\/p>)|(<img[\s\S]*?>)/g
              );
              // 图片无<p>包裹特殊处理
            let regP = /<p\b[^<>]*?>[\s\S]*?<\/p>/,
            regImg = /<img[\s\S]*?>/
            tempArr.forEach((item, index) => {
                //let res = regImg.test(item)
                if (regImg.test(item) && !regP.test(item)) {
                    // this.log(index, res)
                    tempArr[index] = item.replace(/(<img[\s\S]*?>)/, "<p> $1 <\/p>")
                }
            })
            
            let obj = res.data;

            //console.log(tempArr.length,tempArr)
            if(tempArr.length>60){
                obj.artical = tempArr.join("").match(
                    /(<p\b[^<>]*?>[\s\S]*?<\/p>[\s\S]*?<p\b[^<>]*?>[\s\S]*?<\/p>[\s\S]*?<p\b[^<>]*?>[\s\S]*?<\/p>[\s\S]*?<p\b[^<>]*?>[\s\S]*?<\/p>[\s\S]*?<p\b[^<>]*?>[\s\S]*?<\/p>[\s\S]*?<p\b[^<>]*?>[\s\S]*?<\/p>)|(<p\b[^<>]*?>[\s\S]*?<\/p>[\s\S]*?<p\b[^<>]*?>[\s\S]*?<\/p>[\s\S]*?<p\b[^<>]*?>[\s\S]*?<\/p>[\s\S]*?<p\b[^<>]*?>[\s\S]*?<\/p>[\s\S]*?<p\b[^<>]*?>)/g
                )
            }else if(tempArr.length>30){
                obj.artical = tempArr.join("").match(
                    /(<p\b[^<>]*?>[\s\S]*?<\/p>[\s\S]*?<p\b[^<>]*?>[\s\S]*?<\/p>[\s\S]*?<p\b[^<>]*?>[\s\S]*?<\/p>[\s\S]*?<p\b[^<>]*?>[\s\S]*?<\/p>)|(<p\b[^<>]*?>[\s\S]*?<\/p>)|(<img[\s\S]*?>)/g
                )
            }else if(tempArr.length>15){
                obj.artical = tempArr.join("").match(
                    /(<p\b[^<>]*?>[\s\S]*?<\/p>[\s\S]*?<p\b[^<>]*?>[\s\S]*?<\/p>[\s\S]*?<p\b[^<>]*?>[\s\S]*?<\/p>)|(<p\b[^<>]*?>[\s\S]*?<\/p>)|(<img[\s\S]*?>)/g
                )
            }else{
                obj.artical = tempArr.join("").match(
                    /(<p\b[^<>]*?>[\s\S]*?<\/p>[\s\S]*?<p\b[^<>]*?>[\s\S]*?<\/p>)|(<p\b[^<>]*?>[\s\S]*?<\/p>)|(<img[\s\S]*?>)/g
                ) 
            }
            this.setState({
                details: true,
                detailContent:obj,
                modularData:[],
            });
            //console.log(obj.artical)
            this.mescroll.scrollTo( 0, 0);
            this.mescroll.endSuccess();
            this.getDataList(type,id);
            this.getLoopList(1)
            
        }).catch( (error) => {
            console.log(error);
        });
    };

    pageChange(pageType,pageNum){//页码或篇章的切换
        if(pageType === 'back'){
            pageNum = pageNum -1;
            this.setState({
                currentPage:pageNum
            });
        }else if(pageType === 'next'){
            if(pageNum === this.state.detailContent.artical.length){
                let id = this.state.modularData[0].id
                this.detailsClick(id)
            }else{
                pageNum = pageNum + 1;
                this.setState({
                    currentPage:pageNum
                });
            }
            
        }else if(pageType === 'moddle'){
            this.setState({
                currentPage:pageNum
            });
        }
        this.mescroll.endSuccess();
        this.mescroll.scrollTo( 0, 0);
        //console.log(pageType)
        //console.log(pageNum,this.state.currentPage);
    };

    mescrollEvent(e){
        let self = this;
        this.mescroll = new MeScroll("mescroll", {
            //请至少在vue的mounted生命周期初始化mescroll,以确保您配置的id能够被找到
            down: {
                use: false,//是否启用下拉刷新
                auto: false, //是否在初始化完毕之后自动执行下拉回调callback; 默认true
                lazyLoad: {
                    use: true,
                    attr: "imgurl", // 网络地址的属性名 (图片加载成功会移除该属性): <img imgurl='网络图  src='占位图''/>
                    // showClass: '', // 图片加载成功的显示动画: 渐变显示,参见mescroll.css
                    delay: 0, // 列表滚动的过程中每500ms检查一次图片是否在可视区域,如果在可视区域则加载图片
                    offset: 200 // 超出可视区域200px的图片仍可触发懒加载,目的是提前加载部分图片
                }
            },
            up: {
                auto: false, //初始化完毕,是否自动触发上拉加载的回调
                isBoth: false, //上拉加载时,如果滑动到列表顶部是否可以同时触发下拉刷新;默认false,两者不可同时触发; 这里为了演示改为true,不必等列表加载完毕才可下拉;
                isBounce: true, //此处禁止ios回弹,解析(务必认真阅读,特别是最后一点): http://www.mescroll.com/qa.html#q10
                callback: self.upCallback, //上拉加载的回调
                // toTop: {
                // //配置回到顶部按钮
                // src: require('./../assets/img/mescroll-totop.png'), //默认滚动到1000px显示,可配置offset修改
                // //offset : 1000
                // warpClass : "mescroll-totop" ,
                // },
                lazyLoad: {
                use: true,
                attr: "imgurl", // 网络地址的属性名 (图片加载成功会移除该属性): <img imgurl='网络图  src='占位图''/>
                // showClass: '', // 图片加载成功的显示动画: 渐变显示,参见mescroll.css
                delay: 0, // 列表滚动的过程中每500ms检查一次图片是否在可视区域,如果在可视区域则加载图片
                offset: 200 // 超出可视区域200px的图片仍可触发懒加载,目的是提前加载部分图片
                }
            }
        });
    };


    upCallback(e){//上拉加载的回调
        let typeTitle =  this.props.match.params.type;
        let beforeId = '';
        if(this.state.modularData.length>0){
            beforeId = this.state.modularData[this.state.modularData.length-1].id;
        }else{
            beforeId = this.props.match.params.id;
        }
        //console.log(typeTitle,beforeId,this.state.modularData.length) 
        this.getDataList(typeTitle,beforeId);
    };

    getLoopList(num){// 首次进入自动上拉加载
        let timeArr = [3000, 4000, 5000, 6000];
        let randomStep = timeArr[Math.floor(Math.random() * 4)];
        let val = num;
        clearTimeout(this.timer);
        if(val<4){
            val++;
            this.timer = setTimeout(() => {
                //console.log(randomStep)
                if(this.state.modularData.length>0){
                    this.upCallback();
                    this.getLoopList(val)
                }
            },randomStep)
        }
        
    }

    getDataList(type,beforeId){
        let reqObj = { params: { type, beforeId },headers: { zone: encodeURI(this.state.city)} };
        this.upCallEvent = axios.get('/a/news.html?',reqObj).then((res) => { 
            //console.log(res.data.data);
            //console.log(this.state.modularData)
            
            let newList = this.state.modularData;
            res.data.data.forEach((item, index) => {
                if (item.type === 'news') {
                    newList.push(item);
                }
            });
            //console.log(newList)
            this.setState({
                modularData: newList
            });
            loadingDialog("hide");
            this.mescroll.endSuccess(); 
        }).catch( (error) => {
            console.log(error);
            loadingDialog("hide");
        });
    };

    componentWillMount() {
        loadingDialog("show"); 
    };

    componentDidMount() {
        this.mescroll = null;
        this.mescrollEvent();
        try {
            let beforeId = this.props.match.params.id;
            this.detailsClick(beforeId); 
        } catch (error) {
            
        }
        
        console.log(this.props,this.todos)
    };
    
    componentWillUnmount() {
        this.mescroll.destroy();
        this.servequest = null;
        this.upCallEvent = null ;
        clearTimeout(this.timer);
        this.timer = null;
    };

    componentDidUpdate() {
        loadingDialog("hide");
    };
    render() {
        return (
            <div className='App'>
                <div className="mescroll" id="mescroll">
                    <Artical  
                        pageChangeEvent={this.pageChange} 
                        details={this.state.details} 
                        page={this.state.currentPage} 
                        content={this.state.detailContent} 
                        routerParams={this.props}
                    />
                    <DetailsContent 
                        detailsEventClick={this.detailsClick} 
                        pageChangeEvent={this.pageChange} 
                        details={this.state.details} 
                        page={this.state.currentPage} 
                        content={this.state.detailContent} 
                        list={this.state.modularData}
                        routerParams={this.props}
                    />
                </div>
            </div>
        )
    }
}

