import React, { Component, PropTypes } from 'react';
import ContentList from './content/contentList';
import Axios from 'axios';
import loadingDialog from './plugin/loadingDialog';
import { connect } from 'react-redux';
import { addTodo, completeTodo, setVisibilityFilter, VisibilityFilters } from './../actions';
let MeScroll = window.MeScroll;
let baseURL = '';
if(process.env.NODE_ENV==='development'){
    baseURL = '';
}else if(process.env.NODE_ENV==='production'){
    baseURL =  'http://wap1.prculture.cn';
}else{
    baseURL =  'http://wap0713.prculture.cn';
}
var axios =  Axios.create({
    baseURL: baseURL
});
class App extends Component {
    constructor(props){
        super(props);
        this.state = {
            modularData:[],
            city:'',
            locate:'',
        };
        this.handleClick = this.handleClick.bind(this);//首次获取列表数据
        this.downCallback = this.downCallback.bind(this);//下拉刷新
        this.upCallback = this.upCallback.bind(this);//上拉加载
    };

    handleClick(index,type,locate,city) {//获取导航栏后或者切换栏目
        //console.log(index);
        sessionStorage.setItem('type',type);
        loadingDialog("show");
        this.setState({
            city:city,
            locate: locate,
        });
        clearTimeout(this.timer);
        this.handleCounterClick(type) 
    };

    handleCounterClick(type,e) {//首次进来列表的获取
        this.mescroll.scrollTo( 0, 0);
        this.setState({
            modularData: []
        });
        //console.log('outside',new Date().getTime());
        //console.log(this.state.locate);
        axios.get('/a/news.html?',{ params: { type }, headers: { zone: encodeURI(this.state.locate)} }).then((res) => { 
            //console.log('axios')
            //console.log(res.data.data)
            let newList = [];
            //console.log('inside',new Date().getTime());
            //newList=res.data.data;
            res.data.data.forEach((item, index) => {
                if (item.type === 'news') {
                    newList.push(item);
                }
            });
            this.setState({
                modularData: newList
            });            
            this.mescroll.endSuccess() 
            if(this.state.modularData.length<10){
                this.upCallback(); 
            }   
            this.getLoopList(1);
            loadingDialog("hide");
        }).catch( (error) => {
            console.log(error);
            loadingDialog("hide");
        });
        //console.log('改变');
        //this.state.mescroll.endSuccess();
        //console.log(this.state.modularData);
        //console.log(this.state);
    };

    mescrollEvent(e){
        let self = this;
        this.mescroll = new MeScroll("mescroll", {
            //请至少在vue的mounted生命周期初始化mescroll,以确保您配置的id能够被找到
            down: {
                use: true,//是否启用下拉刷新
                auto: false, //是否在初始化完毕之后自动执行下拉回调callback; 默认true
                callback: self.downCallback, //下拉刷新的回调
                lazyLoad: {
                    use: true,
                    attr: "imgurl", // 网络地址的属性名 (图片加载成功会移除该属性): <img imgurl='网络图  src='占位图''/>
                    // showClass: '', // 图片加载成功的显示动画: 渐变显示,参见mescroll.css
                    delay: 0, // 列表滚动的过程中每500ms检查一次图片是否在可视区域,如果在可视区域则加载图片
                    offset: 200 // 超出可视区域200px的图片仍可触发懒加载,目的是提前加载部分图片
                }
            },
            up: {
                auto: false, //初始化完毕,是否自动触发上拉加载的回调
                isBoth: false, //上拉加载时,如果滑动到列表顶部是否可以同时触发下拉刷新;默认false,两者不可同时触发; 这里为了演示改为true,不必等列表加载完毕才可下拉;
                isBounce: true, //此处禁止ios回弹,解析(务必认真阅读,特别是最后一点): http://www.mescroll.com/qa.html#q10
                callback: self.upCallback, //上拉加载的回调
                // toTop: {
                // //配置回到顶部按钮
                // src: require('./../assets/img/mescroll-totop.png'), //默认滚动到1000px显示,可配置offset修改
                // //offset : 1000
                // warpClass : "mescroll-totop" ,
                // },
                lazyLoad: {
                    use: true,
                    attr: "imgurl", // 网络地址的属性名 (图片加载成功会移除该属性): <img imgurl='网络图  src='占位图''/>
                    // showClass: '', // 图片加载成功的显示动画: 渐变显示,参见mescroll.css
                    delay: 0, // 列表滚动的过程中每500ms检查一次图片是否在可视区域,如果在可视区域则加载图片
                    offset: 200 // 超出可视区域200px的图片仍可触发懒加载,目的是提前加载部分图片
                }
            }
        });
    };

    downCallback(e){//下拉刷新的回调
        document.querySelector(".mescroll-upwarp").style.display = "none";
        let typeTitle =  this.props.match.params.type;
        let beforeId = this.state.modularData[this.state.modularData.length-1].id;
        //console.log(typeTitle,beforeId,this.state.modularData.length)    
        this.setState({
            modularData: []
        });
        console.log('downback',new Date().getTime());
        this.getDataList(typeTitle,beforeId);
        this.getLoopList(1);
    };

    upCallback(e){//上拉加载的回调
        let typeTitle =  this.props.match.params.type;
        if(this.state.modularData.length>0){
            let beforeId = this.state.modularData[this.state.modularData.length-1].id;
            this.getDataList(typeTitle,beforeId);
        }
        
    };

    getLoopList(num){// 首次进入自动上拉加载
        let timeArr = [3000, 4000, 5000, 6000];
        let randomStep = timeArr[Math.floor(Math.random() * 4)];
        let val = num;
        clearTimeout(this.timer);
        //console.log('timer',this.timer)
        if(val<4){
            val++;
            this.timer = setTimeout(() => {
                //console.log(randomStep)
                this.upCallback();
                this.getLoopList(val)
            },randomStep)
        }
        
    }

    getDataList(type,beforeId){
        let reqObj = { params: { type, beforeId },headers: { zone: encodeURI(this.state.city)} };
        this.upCallEvent = axios.get('/a/news.html?',reqObj).then((res) => { 
            //console.log(res.data.data);
            //console.log(this.state.modularData)
            let newList = this.state.modularData;
            res.data.data.forEach((item, index) => {
                if (item.type === 'news') {
                    newList.push(item);
                }
            });
            //console.log(newList)
            this.setState({
                modularData: newList
            });
            loadingDialog("hide");
            this.mescroll.endSuccess(); 
        }).catch( (error) => {
            console.log(error);
            loadingDialog("hide");
        });
    }

    componentWillMount() {
        loadingDialog("show");
    }

    componentDidMount() {
        console.log(this.props,this.check)
        this.mescrollEvent();
        let type = this.props.match.params.type ||''
        this.handleCounterClick(type)
    }
    componentDidUpdate (prevProps) {
        if(prevProps.match.params.type !== this.props.match.params.type){
            let type = this.props.match.params.type ||''
            this.handleCounterClick(type)
        }else if(prevProps.location.search !==this.props.location.search && this.props.location.search){
            let type = this.props.match.params.type ||''
            //console.log(prevProps.location.search,'进来',this.props.location.search)
            this.handleCounterClick(type)
        }
        //console.log(prevProps);
        //console.log('route',this.props)
        //console.log(prevState);
    }


    componentWillUnmount() {
        this.mescroll.destroy();
        this.upCallEvent = null ;
        clearTimeout(this.timer);
        this.timer = null;
        //console.log('列表')
    };

    render() {
        const { dispatch, visibleTodos, visibilityFilter } = this.props;
        return (
            <ContentList  routerParams={this.props} list={this.state.modularData} />
        )
    }
}
function selectTodos(todos, filter) {
    switch (filter) {
    case VisibilityFilters.SHOW_ALL:
      return todos;
    case VisibilityFilters.SHOW_COMPLETED:
      return todos.filter(todo => todo.completed);
    case VisibilityFilters.SHOW_ACTIVE:
      return todos.filter(todo => !todo.completed);
    }
  }
  
  // Which props do we want to inject, given the global state?
  // Note: use https://github.com/faassen/reselect for better performance.
  function select(state) {
    return {
      visibleTodos: selectTodos(state.todos, state.visibilityFilter),
      visibilityFilter: state.visibilityFilter
    };
  }
  
  // Wrap the component to inject dispatch and state into it
  export default connect(select)(App);

