import React from 'react';
import HeaderContent from './header/header';
import ContentList from './content/contentList';
import Axios from 'axios';
import {Route, Switch,Redirect} from 'react-router-dom';
import loadingDialog from './plugin/loadingDialog';
import Message from './Message';
import App from './App';
let MeScroll = window.MeScroll;
let baseURL = '';
if(process.env.NODE_ENV==='development'){
    baseURL = '';
}else if(process.env.NODE_ENV==='production'){
    //baseURL =  'https://4g.dzkandian.com';
}else{
    //baseURL =  'https://4g.dzkandian.com';
}
var axios =  Axios.create({
    baseURL: baseURL
});
export default class NewApp extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            modularData:[],
            city:'',
            locate:'',
        };
        
    };

    componentWillMount() {
        
        loadingDialog("show");
    }

    componentDidMount() {
        //console.log(this.props)
    }
    
    componentWillUnmount() {
       // this.mescroll.destroy();
        this.upCallEvent = null ;
        clearTimeout(this.timer);
        this.timer = null;
        //console.log('列表')
    };

    render() {
        return (
            <div className='App'>
                <HeaderContent   routerParams={this.props} />
                <Switch>
                    <Route exact path="/home/index/:type"  component={App}/>
                    <Route exact  path="/home/artical/:type/:id" component={Message}/>
                </Switch>
                {/* <ContentList  routerParams={this.props} list={this.state.modularData} /> */}
            </div>
        )
    }
}


