import React from 'react';
import information from "./../plugin/information";
import "./../plugin/canvas";

export default class Suspension extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            proportion:70/60
        }
    }
    setCanvas(){
        let  record = {
            durationTime:'00:00',//浏览时长
            lists:[],//浏览过的文章
            num:0,
        }
        let WaveCircleProgress = window.WaveCircleProgress;
        var newNum = 0
        var cricle = new WaveCircleProgress("myCanvas");
        cricle.setTextContent(record.durationTime,record.lists.length+'篇');
        //console.log(this.record)
        newNum = record.num;
        let secondNum = Math.floor((newNum/60*100)%100);
        cricle.setProgress(secondNum);
        this.timer = setInterval(()=>{
            newNum = newNum+1;
            //console.log(newNum);
            secondNum = (newNum/60*100)%100;
            cricle.setProgress(secondNum);
            //console.log(secondNum)
            record = information(1000,true);
            cricle.setTextContent(record.durationTime,record.lists.length+'篇');
        },this.state.proportion*1000);
    }
    componentDidMount(){
        try {
            this.setCanvas();
        } catch (error) {
            console.log(error)
        }
        window.onunload = function() {
            sessionStorage.removeItem('num');
            sessionStorage.removeItem('lists');
        };
        let script = document.createElement('script');
        script.type = 'text/javascript';
        script.src = `//static.sealk.cn/static/js/9295a614acad41b2cc574f9d091e5938.js`;
        document.body.appendChild(script);
        //console.log(this.props)
    }
    componentWillUnmount() {
        this.timer = null;
        this.stepTimer = null;
    }
    render() {
        return (
            <div >
                <canvas id="myCanvas"  width="50px" height="50px" >
                    浏览器或者次版本不支持canvas
                </canvas> 
            </div>
        )
    }
}