import React from 'react';
export default class ModularFirst extends React.Component {
    constructor(props){
        super(props);
        this.state = {};
    }
    render() {
        return (
            <div className="news-wrap"  >
                <div className="img-wrap" >
                        {this.props.item.images.slice(0, 1).map((urls,val) =>
                            <div className="pic" key={val} imgurl={urls} style={{backgroundImage: "url(https://static.dzkandian.com/images/loadwap.png)"}}>
                            </div>
                        )}
                    {/* <div  imgurl={this.props.item.images.slice(0, 1)} style={{backgroundImage: "url(https://static.dzkandian.com/images/loadwap.png)"}}>
                    </div>             */}
                </div>
                <div className="txt-wrap">
                    <h3 className="dz-ellipsis-2" style={{"WebkitBoxOrient": "vertical"}}>
                        {this.props.item.title}
                    </h3>
                    <p className="tags clearfix">
                        <em className="tag tag-src">{this.props.item.source}</em>
                        <em className="tag tag-view">
                        {this.props.item.updateTime.substring(0, 10)}
                        </em>
                    </p>
                </div>
            </div>
        )
    }
}