import React from "react";
import {HashRouter, Route, Switch,Redirect} from 'react-router-dom';
import NewApp from './../components/NewApp';
import Suspension from './../components/header/suspension';
import initReactFastclick from 'react-fastclick';
initReactFastclick();
export default class BasicRoute extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            check:false
        };
    }
    componentDidMount(){       
        try {
            let controlCanvas  = window.controlCanvas;
            let check = controlCanvas();
            this.setState({
                check: check,
            });
        } catch (error) {
            //console.log(error)
        }       
    }
    render() {
        return (
            <HashRouter>
                <Switch>
                    <Route  path="/home"  component={NewApp}/>
                    <Redirect to='/home/index' />
                </Switch>
                {this.state.check ? <Suspension />:''} 
            </HashRouter>
        )
    }
}
