import React from 'react';
import ModularFirst from './../modular/ModularFirst';
import ModularSecond from './../modular/ModularSecond';
export default class ContentList extends React.Component {
    constructor(props){
        super(props);
        this.state = {

        };
    }
    detailsClick(id){
        //console.log('去详情')
        document.querySelector(".mescroll-upwarp").style.display = "none";
        let  typeTitle = this.props.routerParams.match.params.type
        this.props.routerParams.history.replace('/home/artical/'+typeTitle+'/'+id);
    }
    render() {
        return(
            <div className="mescroll" id="mescroll">
                <div style={{padding: "0 10px"}}>
                    {this.props.list.map((listItem,index) =>
                        <div key={index} onClick={(e) => this.detailsClick(listItem.id, e)}>
                            {!listItem.images ||listItem.images.length>=3?(<ModularSecond  item={listItem} /> ):(<ModularFirst   item={listItem} /> )}
                        </div>
                    )}
                </div>
            </div>
        )
    }
}